@import "../../constants/_variables.scss";

$button-size: 50px;

.home-screen-wrapper {
  .intro-text {
    font-size: $h3;
    text-align: center;
  }
  .scroll-to-top-wrapper {
    position: fixed;
    bottom: 30px;
    right: 30px;
    width: $button-size;
    height: $button-size;
    border-radius: 50%;
    z-index: 7;
    display: none;
    background-color: $purple_dark;

    &.active {
      display: block;
      text-shadow: $shadow1;
    }
  }
  .footer {
    display: flex;
    justify-content: center;
    font-size: 0.9rem;
    padding: 2.5rem;
  }
}